<template>
  <div class="box">
    <mt-header title="查询预约信息" color="rgb(0,204,153)">
    	<router-link to="/" slot="left">
    		<mt-button icon="back">返回</mt-button>
    	</router-link>
    </mt-header>
    <ul>
      <li class="list" v-for="item in resultlist" @click="resultClick(item)">
        <div class="top">预约编号：{{item.ReserveNo}}</div>
        <div class="center">
          <div class="left">
            <div>姓名：{{item.Name}}</div>
            <div>预约单位：{{item.HospitalName}}</div>
            <div>体检地址：{{item.HospitalAddress}}</div>
          </div>
          <div class="right">
            <span v-if="item.State == -1" style="color: rgba(0,0,0,0.6);">已取消</span>
            <span v-if="item.State == 0" style="color: #23ACF8;">未体检</span>
            <span v-if="item.State == 1" style="color: blue;">体检完成</span>
            <span v-if="item.State == 2" style="color: yellowgreen;">制卡完成</span>
            <span v-if="item.State == -2" style="color: rgba(0,0,0,0.6);">已过期</span>
          </div>
          <div></div>
        </div>
        <div class="bottom">预约日期：{{item.Date}}</div>
      </li>
    </ul>
  </div>
</template>

<script>
  const axios = require('axios')
  import { Indicator,Toast } from 'mint-ui'
  export default {
    data(){
      return{
        resultlist: [],
        idcard:''
      }
    },
    methods:{
      resultClick(item){
        this.$router.push({
        	path: '/information',
        	query: {Item:item}
        })
      }
    },
    created(){
      this.idcard = sessionStorage.getItem("IDCard")
      Indicator.open('加载中...')
      axios({
        method: 'post',
        url: 'https://www.photo2030.com/webapi/api/Reserve/GetOpenReserveList',
        data: {
          IDCardNo: this.idcard,
          Token: 'B68DE9D1F8584D7099BCE47144D0351C'
        }
      }).then(res => {
        Indicator.close()
        if(res.data.IsSuccess){
          this.resultlist = res.data.Data
        }else{
          Toast({
            message: res.data.Message,
            position: 'middle',
            duration: 3000
          })
        }

      }).catch(error => {
        Indicator.close()
        Toast({
          message: '服务器错误',
          position: 'middle',
          duration: 3000
        })
      })
    }
  }
</script>

<style scoped>
  .box{
    width: 100%;
    height: 100%;
    overflow-y: auto;
    position: relative;
  }
  ul{
    padding-top: 0.7rem;
    width: 100%;
    box-sizing: border-box;
  }
  .list{
    background: rgba(0,0,0,0.1);
    width: 100%;
    padding: 0 0.3rem;
    margin: 0.3rem 0;
    box-sizing: border-box;
  }
  .list>.top{
    width: 100%;
    border-bottom: 1px solid #ccc;
    height: 0.6rem;
    line-height: 0.6rem;
  }
  .list>.center{
    width: 100%;
    border-bottom: 1px solid #ccc;
    padding: 0 0.3rem;
    box-sizing: border-box;
    line-height: 0.6rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .list>.center>.right{
    border: 1px solid #000;
    width: 2rem;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .right>span{
    font-size: 0.3rem;
  }
  .list>.bottom{
    height: 0.6rem;
    width: 100%;
    text-align: right;
    line-height: 0.6rem;
    border-top: 1px solid #ccc;
  }
  .mint-header{
    width: 100%;
    position: fixed;
    top: 0;
  	background: rgb(0,204,153);
  }
</style>
